.grid2 {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr 1fr;
  grid-gap: 20px;
}


.card-image {
  grid-column: 3 span;
  max-height: 250px;
  object-fit: cover;
  display: flex;
  img {
    width: 100%;
    max-height: 100%;
    flex-grow: 1;
  }
}
.card-image--wide {
  grid-column-start: 1;
  grid-column-end: 7;
}
.card-image--wide2 {
  grid-column-start: 4;
  grid-column-end: 10;
}


.section--footer {
  position: relative;
  background-color: $light-color;
  flex-direction: column;
  align-items: center;
  .slider {
    padding: 80px 0 200px 0;
    background-color: $light-color;
    width: 100vw;
    flex-direction: column;
    max-width: 1180px;
    flex-grow: 1;
    align-items: center;
    overflow-x: hidden;
    .container__heading {
      text-align: center;
      justify-content: center;
      margin: 0 auto;
      .btn {
        margin: 0 auto;
      }
    }

    .hidden-slider {
      display: none;
    }
    .gallery, .hidden-slider {
      margin-top: 40px;
      margin-bottom: 50px;
    }
    @include tablet  {
      padding-bottom: 50px !important;
      align-items: center;
      .container__heading {
        text-align: left;
      }
      .gallery {
        display: none;
      }
      .hidden-slider {
        display: block;
        padding-left: 20px;
        padding-right: 20px;
        .swiper {
          .swiper-wrapper {
            .swiper-slide {
              width: 100%;
             .card-image--wide {
                max-width: 100%;
                height: 250px;
             }
            }
          }
        }
      }

  }


  }
  .footer__content {
    padding: 175px 0 80px 0;
    position: relative;
    min-width: 100vw;
    background-color: $dark-green;
    display: flex;
    max-width: 1180px;
    justify-content: center;
    .form-area {
      z-index: 600;
      width: 100vw;
      position: absolute;
      z-index: 999;
      top: -150px;
      height: fit-content;
      display: flex;

      .gallery-form {
        margin: 0 auto;
        max-width: 1180px;
        box-shadow: 0 0 15px 0 rgba(24, 18, 30, 0.12);
        background-color: $light-color;
        &__container {
          padding: 40px 85px;
          .container__heading {
            text-align: left;
            margin-left: 108px;
          }
          .form-inner {
            max-width: 535px;
            .form__submit {
              padding-top: 12px;
              padding-bottom: 12px;
              display: inline-block;
            }
            .form__email {
              padding: 12px 16px;
              display: inline-block;
              border: none;
              border-radius: 4px;
              margin-right: 16px;
              background-color: $gray-bg;
              &::placeholder {
                font-weight: 300;
                font-size: 14px;
                line-height: 143%;
                color: $dark-color;
                border-radius: 4px;
              }
            }
            p {
                font-weight: 600;
                font-size: 14px;
                line-height: 129%;
                color: $dark-color;
                a {
                  text-decoration: underline;
                }
            }
            @include tablet  {
              display: flex;
              flex-direction: column;
              margin-top: 10px;
              .form__email {
                margin-bottom: 20px;
                max-width: 310px;
                flex-grow: 1;
              }
            }
          }
          @include tablet  {
            padding: 0;
            flex-direction: column;
            .container__heading {
              text-align: left;
              margin-left: 0;
            }
          }
        }

      }
      @include tablet {
        background-color: $light-color;
        padding: 15px 15px 50px 15px ;
        position: static;

        .gallery-form {
          padding: 25px 15px;
        }
      }
    }
    .main-content {
      flex-grow: 1;
      display: flex;
      max-width: 1180px;
      justify-content: space-between;
      .heading {
        font-weight: 600;
        font-size: 20px;
        line-height: 120%;
        color: $gray-bg;
      }
      .text {
        font-weight: 300;
        font-size: 14px;
        line-height: 143%;
        color: $gray-bg;
        padding: 0;
        margin: 15px 0 80px 0;
      }
      &__contacts {
        max-width: 435px;

        .links {
          font-weight: 600;
          font-size: 14px;
          line-height: 129%;
          text-decoration: underline;
          color: $light-color;
          .email {
            display: inline-block;
            margin-right: 25px;
          }
        }
        .socials {
          margin-top: 15px;
          display: flex;
          gap: 15px;
          .social__link-icon {
            display: inline-block;
            width: 32px;
            height: 32px;
            background: url('../img/socials-skype.svg') no-repeat;
            background-position: center;
            &:hover {
              transition: all ease-in;
              opacity: 0.8;
            }
            &:nth-child(2) {
              background: url('../img/socials-vk.svg') no-repeat;
              background-position: center;
            }
            &:nth-child(3) {
              background: url('../img/socials-pinterest.svg') no-repeat;
              background-position: center;
            }
            &:nth-child(4) {
              background: url('../img/socials-skype.svg') no-repeat;
              background-position: center;
            }
          }
        }
        @include tablet {
          .text {
            margin-bottom: 20px;
          }
        }
      }
      &__nav {
        display: flex;
        flex-direction: column;
        gap: 20px;
        .text {
          margin: 0;
          padding-left: 18px;
          display: flex;
          position: relative;
          width:  fit-content ;
          .new-content {
            position: absolute;
            right: -61px;
            display: inline-block;
            border-radius: 4px;
            padding: 2px 10px;
            width: 44px;
            height: 20px;
            background-color: white;
            font-weight: 700;
            font-size: 12px;
            line-height: 133%;
            text-transform: lowercase;
            color: $dark-green;
          }
          &::before {
            left: 0;
          }
        }

      }
      .nav-1 {
        margin-right: 61px;

      }
      .nav-2 {
        max-width: 250px;
      }
      @include tablet {
       background-color: $dark-green;
       padding: 0 15px 0 15px;
       flex-direction: column;
       gap: 40px;
      }
    }
    @include tablet {
      padding-top: 0 !important;
    }
  }

  .copyright {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: $dark-color;

    .container {
      flex-grow: 1;
      padding: 15px 0;
      .copyright-text {
        font-family: $font-family;
        font-weight: 600;
        font-size: 14px;
        line-height: 129%;
        color: #fff;
      }
      a {
        text-decoration: underline;

      }
      @include tablet {
        padding: 15px;
        justify-content: center;
      }
    }
  }

}
