@import "../mixins";
@import "../vars";
@import "../fonts";
@import "../settings";


.icon--logo {
  max-width: 137px;
  max-height: 72px;
  @include tablet {
    max-width: 84px;
    max-height: 44px;
  }
}
.header {
  position: absolute;
  &__container {
    display: flex;
    justify-content: space-between;
    padding: 15px;
    flex-grow: 1;
  }
}
.list {
  display: flex;
  gap: 24px;
  .list__elem {
    a {
      font-family: $font-family;
      font-weight: 600;
      font-size: 18px;
      color: $light-color;
    }
  }

  }
.nav {
  display: flex;
  align-items: center;
  gap: 50px;
  .burger {
    display: none;
  }

  @include tablet {
    .list {
      display: none;
    }
    .list--modal {
      display: block;
      padding-left: 15px;
     a {

      font-size: 14px;
      line-height: 129%;
     }


    }
    .burger {
      display: block;
      max-width: 30px;
      max-height: 30px;
    }

  }
  @include mobile {
    .btn {
      display: none;
    }
  }
}



.graph-modal {
  background: $dark-green;
  &__container {
    background-color: inherit;
    margin: 0;
    height: 100%;
    width: fit-content;
    padding-left: 15px;
  }
  .graph-modal__close {
    background: url("../img/esc-button.svg") no-repeat;
    background-position: center;
    background-size: cover;
    margin: 18px 9px 0 0;
  }
}
