
.form {
  width: 100%;
  @include flex-layout($options);
  backdrop-filter: blur(6px);
  padding: 40px 43px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.2);
  font-family: $font-family;
  &__label {
    flex-grow: 1;
    max-width: 270px;
    display: flex;
    flex-direction: column;
    .label__subtitle {
      font-family: $font-family;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: $light-color;
      display: inline-block;
      margin-top: 10px;
      margin-left: 10px;

    }
  }
  &__label:nth-child(3) {
    flex-grow: 2;
  }
  .form__btn {
    display: block;
    text-align: center;
    background: $light-color;
    border-radius: 4px;
    line-height: 125%;
    padding: 16px 36px;
    color: #1a3e3e;
    max-width: fit-content;
    height: fit-content;
    width: max-content;
  }
  .form__input {
    border: 1px solid #e1e3e4;
    border-radius: 8px;
    padding: 16px 36px;
    background-color: inherit;
    font-weight: 700;
    font-size: 16px;
    line-height: 125%;
    letter-spacing: 0.01em;
    color: $light-color;
    width: 100%;

    &::placeholder {
      color: inherit;
    }


  }
  @include small-tablet {

    &__label {
      max-width: none;
    }
  }
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.input--select,
.input--number {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance:none;
  background: url("../img/hero-form-arrow.png") no-repeat;
  background-position: center right 16px;

  option {
    color: $dark-color;

  }
}

.input--date {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance:none;
  background: url("../img/hero-form-date.png") no-repeat;
  background-position: center right 16px;

}



