
.section--about {
  padding: 100px 0;
  &__container {
    justify-content: space-between;
    flex-wrap: nowrap;
    flex-grow: 1;

    .container__heading {
      max-width: 580px;
    }

    .container__images {
      position: relative;
      min-width: 35%;
      height: 526px;
      .second-image {
        position: absolute;
        right: 20px;
        top: 105px;

      }
    }
    @include tablet {
      flex-wrap: wrap;
      gap: 0;

      .container__images {

        margin-top: 40px;
        flex-grow: 1;
        display: flex;
        order: 2;
        .second-image {
          width: 100%;
          position: static;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .first-image {
          display: none;
        }
      }
      .container__heading {
        max-width: none;
      }
    }
  }
}
