.swiper {
  max-width: 100%;
  overflow: hidden;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;
}


.swiper-slide {
  grid-column: 4 span;
  @include mobile {
    width: 80vw;
  }
}
.swiper-slide--row {
  grid-column: 6 span;
}




@media (max-width: 1500px) {
  .slider-2 .swiper-wrapper {

    display: flex;
    column-gap: 0;
  }
}

@media (max-width: 1280px) {

  .slider-1 .swiper-wrapper {
    display: flex;
    column-gap: 0;


  }
}


