*,
*::before,
*::after {
  box-sizing: border-box;
  // outline-color:red;
  // outline-style: dotted;
}
h1,h2,h3,h4,h5,h6 {
  margin: 0;
}
ul {
  padding: 0;
}
.page {
  height: 100%;
  font-family: $font-family, sans-serif;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  &__body {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
  }
}
.icon {
  fill: $light-color;
}
.site-container {
  width: 100vw;
  min-height: 100%;
  width: $container-width;
  @include footerToBottom;
}
.section, header, footer {
  width: 100%;
  display: flex;
  overflow-x: hidden;
  justify-content: center;

}
.main {
  section, .section {
    @include tablet {
      padding: 0;
      .container {
        padding: 50px 15px;
      }
    }
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}


a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  width: 100%;
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  max-width: 1180px;
}


.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}
.btn {
  border-radius: 4px;
  padding: 16px 24px;
  width: fit-content;
  background: $dark-green;
  span {
    font-family: $font-family;
    font-weight: 600;
    font-size: 16px;
    line-height: 125%;
    letter-spacing: 0.01em;
    color: $light-color;
  }
}
.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container {
  .link {
    &:hover {
      color: $hovered-link ;
    }
  }
}
.btn {
  &:hover {
    span {
      color: $hovered-link ;
    }
  }
}
.main {
  width: 100%;
}
.before-arrow-icon  {
  position: relative;
  display: inline-flex;
  align-items: center;
  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 5px;
    height: 9px;
    left: -15px;
    background: url("../img/link-arrow.svg") no-repeat;
    background-size: cover;
    background-position: center;

  }
  &::after {
    content: "";
    position: absolute;
    display: block;
    bottom: -1px;
    margin: 0 auto;
    width: 100%;
    height: 0.7px;
    background-color: $hovered-link;
    transform: scaleX(0);
    transition: transform 0.3s ease;
  }
  &:hover{
    &::before {
      background: url("../img/link-arrow--hovered.svg") no-repeat;

    }
    &::after {
      transform: scaleX(1);
    }
  }
}


.heading {
  max-width: 90vw;
  font-family: $font-family;
  font-weight: 80;
  &__title {
    font-family:0;
    font-size: 32px;
    line-height: 120%;
    color: $dark-color
  }
  &__subtitle {
    font-family: $font-family;
    font-weight: 400;
    font-size: 24px;
    line-height: 83%;
    color: $hovered-link;
    margin-bottom: 10px;
    display: inline-block;
  }
  &__text {
    font-family: $font-family;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: $dark-color;
    display: inline-block;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 40px;
    margin: 22px 0 40px 0;
    .list__item {
      padding-left: 66px;
      position: relative;
      .list__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 111%;
        letter-spacing: 0.01em;
        color: $dark-color;
      }
      .list__text {
        margin: 5px 0 0 0;
        font-weight: 400;
        font-size: 14px;
        line-height: 143%;
        color: $black;
      }
      &::before {
        content: "";
        position: absolute;
        display: block;
        width: 50px;
        height: 50px;
        left: 0px;
        background: url("../img/svg/programs-1.svg");
        background-position: center;
      }
      &:nth-child(2) {
        &::before {
          background: url("../img/svg/programs-2.svg");
        }
      }
      &:nth-child(3) {
        &::before {
          background: url("../img/svg/programs-3.svg");
        }
      }
    }
  }
  .btn {
    display: block;
  }
  @include tablet {
    &__title {
      margin: 0;
    }
    &__text {
      margin: 30px 0 25px 0;
    }
    .btn {
      margin: 0 auto;
    }
  }
}
