
.card {
  display: flex;
  justify-content: center;
  .card-container {
    display: block;
    min-height: 500px;
    max-width: 380px;
    flex-grow: 1;
    border-radius: 30px;
    position: relative;
    display: flex;
    align-items: flex-end;
    background: url("../img/cards-bg-1.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    &__rating {
      position: absolute;
      right: 0;
      top: 48px;
      border-radius: 4px 0 0 4px;
      max-width: fit-content;
      padding: 6px 11px;
      background-color: rgba(253, 253, 253, 0.4);
      span {
        padding-left: 25px;
        position: relative;
        font-weight: 700;
        font-size: 14px;
        line-height: 129%;
        color: $dark-green;
        &::before {
          content: "";
          position: absolute;
          display: block;
          width: 24px;
          height: 24px;
          background: url("../img/popular-tour-card-rating.svg") no-repeat;
        }
      }
    }

    &__price-container {
      width: 100%;
      background: rgba(255, 255, 255, 0.2);
      backdrop-filter: blur(6px);
      padding: 25px 22px 23px 22px;
      display: flex;
      justify-content: space-between;
      border-radius: 0 0 30px 30px;
      .card__heading {
        max-width: 210px;
        display: flex;
        flex-direction: column;
        .card-title {
          font-weight: 700;
          font-size: 24px;
          line-height: 120%;
          color: $light-color;

        }
        .card-subtitle {
          font-weight: 400;
          font-size: 14px;
          font-weight: 300;
          line-height: 129%;
          color: $light-color;
        }
      }
      .card__price {
        background: rgba(255, 255, 255, 0.3);
        padding: 8px 16px;
        border-radius: 4px;
        height: fit-content;
        margin-left: 25px;
        .price {
          font-weight: 700;
          font-size: 14px;
          line-height: 120%;
          color: $light-color;
          text-wrap: nowrap;
        }
      }
    }
    @include mobile {
      min-width: auto;
      &__price-container {
        .card__heading {
          .card-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;

          }
          .card-subtitle {
            display: none;
          }
          .card__price {

            .price {
              font-weight: 700;
              font-size: 16px;
              line-height: 112%;

            }
          }

        }

      }
    }
  }
}


.card-2 {
  .card-container {
    background: url("../img/cards-bg-2.jpg");
  }
}
.card-3 {
  .card-container {
    background: url("../img/cards-bg-3.jpg");
  }
}

.card--horizontal {
  .card-container {
    background: none;
    background-color: $light-color;
    box-shadow: 0 0 15px 0 rgba(24, 18, 30, 0.12);
    padding: 15px;
    min-height: 312px;
    max-width: 580px;
    border-radius: 10px;
    display: flex;
    .card-container__image {
      .card-image {
        width: 230px;
        max-height: 280px;
        display: flex;
        @include tablet {
          width: 100%;
        }
        .image {
          width: 100%;
          flex-grow: 1;
        }
      }
    }
    .card-container__content {
      height: 100%;
      padding-left: 24px;
      display: flex;
      flex-direction: column;
      .card__heading {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        .card-title {
          font-weight: 700;
          font-size: 24px;
          line-height: 133%;
          color: $dark-green;
          margin-bottom: 24px;
        }
        .card-subtitle {
          font-weight: 400;
          font-size: 14px;
          line-height: 143%;
          color: $dark-color;
          flex-grow: 1;
        }
      }
      .card__price {
        display: flex;
        justify-content: space-between;
        .price {
          font-weight: 700;
          font-size: 14px;
          line-height: 143%;
          color: $hovered-link;
        }
        .link {
          font-weight: 700;
          font-size: 14px;
          line-height: 143%;
        }
      }
    }
    @include tablet {
      flex-direction: column;
      padding: 0;
      .card-container__image {
        width: 100%;
        height: 280px;
      }
      .card-container__content {
        padding: 15px;
        width: 100%;
        .card__price {
          margin-top: 15px;
        }
      }
    }
  }
}
