

.section--hero {
  @include bg-cover;
  background: url('../img/hero-bg.png');
  background-size: cover;
  background-repeat: no-repeat;
  padding: 82px 0;
  min-height: 985px;
  &__container {
    flex-direction: column;
    justify-content: flex-end;
    .btn {
      display: none;
    }
  }
  @include tablet {
    &__container {
      padding: 15px;
      .btn {
        display: block;
      }
      .btn--hero {
        padding: 16px 24px;
        background-color: $light-color;
        span {
          color: $dark-color;
        }
      }
    }
    min-height: 600px;
    .form {
      display: none;
    }
  }
}
.title {
  font-family: $second-family;
  font-family: $second-family;
  font-weight: 700;
  font-size: 50px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: $light-color;
  @include tablet {
    font-size: 35px;
  }
  @include mobile {
    font-size: 28px;
  }
}
