.section--programs {
  background-color: $gray-bg;
  padding: 100px 0;
  &__container {
    .container__images {
      min-width: auto;
      max-width: 510px;
      margin-top: 40px;
      display: flex;
      gap: 10px;
      position: static;
      flex-wrap: wrap;
      max-height: 510px;
      .second-image {
        position: static;
      }
      picture {
        .image {
          max-width: 250px;
          max-height: 250px;

        }
      }
      @include tablet {
        display: none;
      }
    }

  }
}
